.feedback {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: flex-start;
  padding-bottom: 50px;
  width: 100%;
  box-sizing: border-box;

  @media (max-width: 1245px) {
    flex-direction: column;
    align-items: center;
  }

  &__text {
    width: 31%;
    box-sizing: border-box;

    @media (max-width: 1245px) {
      width: 40%;
    }

    @media (max-width: $mobile) {
      width: 100%;
    }

    & p {
      margin: 0;
      margin-bottom: 13px;
      font-style: normal;
      font-weight: normal;
      font-size: 13px;
      line-height: 150%;

      color: $primary-color;
      opacity: 0.5;

      &:first-child {
        font-weight: 500;
        font-size: 17px;
        line-height: 150%;
        opacity: 0.87;
      }
    }
  }

  &__form {
    display: flex;
    flex-wrap: wrap;

    width: 67%;
    box-sizing: border-box;

    @media (max-width: 1245px) {
      width: 100%;
      max-width: 768px;
    }

    @media (max-width: $mobile) {
      width: 100%;
      max-width: 500px;
    }

    & label {
      width: 33%;
      margin-right: 24px;
      margin-bottom: 20px;
      box-sizing: border-box;

      @media (max-width: $mobile) {
        width: 100%;
        margin-right: 0;
      }
    }

    & input {
      padding: 16px 20px;
      width: 100%;
      font-family: $mainFontMedium;
      font-style: normal;
      font-weight: 500;
      font-size: 17px;
      line-height: 26px;
      font-variant: small-caps;

      color: $primary-color;
      opacity: 0.5;
      outline: none;
      border: 2px solid #e3e3e3;
      box-sizing: border-box;
      border-radius: 80px;

      &:focus {
        border-color: $warning-color;
      }
    }

    & button {
      width: 27%;
      margin-bottom: 20px;
      padding: 10px 20px;
      font-style: normal;
      font-weight: 500;
      font-size: 17px;
      line-height: 26px;
      font-variant: small-caps;

      color: #ffffff;

      text-shadow: 0 1px 4px rgba(0, 0, 0, 0.12);
      background: linear-gradient(140.64deg, #ffc341 20.46%, #ffd701 65.31%);
      border-radius: 80px;
      outline: none;
      border: none;
      background-color: transparent;

      cursor: pointer;

      &:focus,
      &:hover {
        background: linear-gradient(143.55deg, #ffc341 -50.3%, #ffd701 63.1%);
      }

      &:active {
        background: radial-gradient(
            150% 119.49% at 50% 50.85%,
            rgba(0, 0, 0, 0) 30.67%,
            rgba(3, 3, 3, 0.24) 100%
          ),
          linear-gradient(143.55deg, #ffc341 -50.3%, #ffd701 63.1%);
      }

      @media (max-width: $mobile) {
        width: 100%;
      }
    }
  }
}
